import React, { Component } from 'react';
import './App.css';

import logoSVG from './assets/logo.png';
import eventsSVG from './assets/events.svg';
import careSVG from './assets/care.svg';
import teamSVG from './assets/team.svg';
import hospitalitySVG from './assets/hospitality.svg';
import missionBG from './assets/mission-bg.png';
var copyrightMessage = 'Copyright © Host Inc 2018';
var name = 'Host eSports';
var location = 'San Francisco, California';
var headerTitle = 'Bridging the gap between Players and Events';
var mission = 'White-glove facilitation of player experiences at events and tournaments';
var missionSubtitle = 'Host focuses on planning and providing players and teams with streamline event experiences.';

//Our work

const work = {
  // Work History
  // you must include http:// or https:// before URLs
  test: [
    {
      artURL: 'https://hostesports.com/img/recharge.jpg',
      name: 'Event 20XX 1',
      desc: 'Description',
      year: '2017',
      url: 'http://google.com'
    },
    {
      artURL: 'https://hostesports.com/img/cupcakes.jpg',
      name: 'Event 20XX 2',
      desc: 'Description',
      year: '2017',
      url: 'http://google.com'
    },
    {
      artURL: 'https://hostesports.com/img/conch.jpg',
      name: 'Event 20XX 3',
      desc: 'Description',
      year: '2017',
      url: 'http://google.com'
    },
    {
      artURL: 'https://hostesports.com/img/lounge.jpg',
      name: 'Event 20XX 4',
      desc: 'Description',
      year: '2017',
      url: 'http://google.com'
    }
  ]
};

const services = {
  // Current Services
  // you must include http:// or https:// before URLs
  test: [
    {
      artURL: eventsSVG,
      name: 'Events',
      desc: 'We create VIP experiences for players at events and after-parties, because they deserve it.',
      url: 'http://google.com'
    },
    {
      artURL: careSVG,
      name: 'Player Care',
      desc: 'Happy players are better players, we ensure players play their best game.',
      url: 'http://google.com'
    },
    {
      artURL: teamSVG,
      name: 'Team Building',
      desc: 'Host believes happy teams are better teams. We help that happen.',
      url: 'http://google.com'
    },
    {
      artURL: hospitalitySVG,
      name: 'Player Hospitality',
      desc: 'Happy players are better players, we ensure players play their best game.',
      url: 'http://google.com'
    },
  ]
};

const Header = () => (
  <header id='Header'>
    <div class='row'>
      <div className='header-info mx-auto'>
        <img className='logo' src={ logoSVG } />
        <h1 className='h1'>{ name }</h1>
        <h3 className='h3'>{ location }</h3>
        <a className='button' href="#Service">Learn More</a>
      </div>
    </div>
  </header>
);

const Mission = () => (
  <div className='mission' id='Mission'>
    <div className='row'>
      <div className='mission-info'>
        <h3 className='h3'>{ mission }</h3>
        <p className='p'>{ missionSubtitle }</p>
      </div>
    </div>
  </div>
);

const Footer = () => (
  <footer id='Footer'>
    <div className='row'>
      <div className='footer-info text-center col-sm-12'>
        <p className='h4'>{ name }</p>
        <p className='p'>{ location }</p>
        <a className='a button' href='mailto:info@hostesports.com'>info@hostesports.com</a>
        <a className='a button' href='https://twitter.com/hostesports'>Follow @hostesports</a>
        <p className='copyright'>{ copyrightMessage }</p>
      </div>
    </div>
  </footer>
);

const Work = ({ work }) => (
  <div className='projects px-3' id='Work'>
    <div className='col-sm-12'>
      <h1 className='h1 text-center'>Our Work</h1>
    </div>
    <div className='col-sm-12'>
      <div className='row'>
        { work.map((work, ind) => (
          <div className={ `col-xs work p-2` }>
            <img src={ work.artURL } />
            <div className='worksubline'>
              <div className='workinfo'>
                <div className='workname'><a target='blank' className='workbutton' href={ work.url }>
                  { work.name }
                  </a>
                </div>
                <div className='workdesc'>{ work.desc }</div>
                <div className='workyear'>{ work.year }</div>
              </div>
            </div>
          </div>
        )) }
      </div>
    </div>
  </div>
);

const Services = ({ services }) => (
  <div className='service' id='Service'>
    <div className='col-sm-12 text-center'>
      <h1 className='h1'>Our Services</h1>
    </div>
    <div className='d-flex flex-wrap shell mx-auto col-sm-12'>
      { services.map((services, ind) => (
        <div className={ `services${ ind%2===1 ? ' alt' : '' } col-sm-6` }>
          <img src={ services.artURL } />
          <div className='servicesSubline'>
            <div className='service-info'>
              <div className='servicesName'>{ services.name }</div>
              <div className='servicesDesc'>{ services.desc }</div>
            </div>
          </div>
        </div>
      )) }
    </div>
  </div>
);

class App extends Component {
  constructor() {
    super();

    this.state = {
      currentWork: work.test,
      currentServices: services.test
    };
  }

  render() {
    const { currentWork, currentServices } = this.state;
    return (
      <div className='app'>
        <div className='container'>
          <Header/>
          <Mission />
            <Services
              services={ currentServices }
              />
          <Work
            work={ currentWork }
            />
          <Footer />
        </div>
      </div>
    );
  }
}

export default App;



